"use client";

import * as React from "react";
import Link from "next/link";
import { useTheme } from "next-themes";
import { Menu, Moon, Sun, Code2 } from "lucide-react";

import { Button } from "../ui/button"; // adjust path if needed
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "../ui/dropdown-menu";
import { Sheet, SheetContent, SheetTrigger } from "../ui/sheet";

const navLinks = [
  { href: "#about", label: "About" },
  { href: "#skills", label: "Skills" },
  { href: "#projects", label: "Projects" },
  { href: "#certifications", label: "Certifications" },
  { href: "#contact", label: "Contact" },
];

function ThemeToggle() {
  const { setTheme } = useTheme();

  return (
    <DropdownMenu>
     <DropdownMenuTrigger asChild>
 <Button 
  variant="ghost" 
  size="icon" 
  className="p-1 rounded-full border-0.5 border-gray-300 dark:border-gray-600 hover:bg-gray-100 dark:hover:bg-gray-800">
  <Sun className="h-10 w-10 rotate-0 scale-100 transition-all dark:-rotate-90 dark:scale-0 text-[#1da1f2]" fill="currentColor" stroke="currentColor" strokeWidth={3}/>
  <Moon className="absolute h-10 w-10 rotate-90 scale-0 transition-all dark:rotate-0 dark:scale-100 text-[#1da1f2]" fill="currentColor"/>
  <span className="sr-only">Toggle theme</span>
</Button>
</DropdownMenuTrigger>
      <DropdownMenuContent align="end">
        <DropdownMenuItem onClick={() => setTheme("light")}>Light</DropdownMenuItem>
        <DropdownMenuItem onClick={() => setTheme("dark")}>Dark</DropdownMenuItem>
        <DropdownMenuItem onClick={() => setTheme("system")}>System</DropdownMenuItem>
      </DropdownMenuContent>
    </DropdownMenu>
  );
}

export function Navigation() {
  const [isOpen, setIsOpen] = React.useState(false);

  return (
        <header className="sticky top-0 z-50 w-full border-b border-border/40 bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60 shadow-2xl">
      <div className="container flex h-14 items-center justify-between px-3 sm:px-10 md:px-20 lg:px-40">
        {/* Left: Name / Logo */}
        <Link href="/" className="font-bold text-lg sm:text-lg md:text-lg lg:text-lg">
          Randel Yumul
        </Link>

        {/* Right: Navigation + Theme Toggle + Mobile Menu */}
        <div className="flex items-center space-x-2 sm:space-x-3 md:space-x-4">
          {/* Desktop Navigation */}
          <nav className="hidden md:flex items-center space-x-4 lg:space-x-6 text-sm font-medium">
            {navLinks.map(({ href, label }) => (
              <Link
                key={href}
                href={href}
                className="transition-colors hover:text-[#1da1f2] text-black dark:hover:text-[#1da1f2] dark:text-white"
              >
                {label}
              </Link>
            ))}
          </nav>

          {/* Theme Toggle */}
          <ThemeToggle />

          {/* Mobile Menu */}
          <Sheet>
            <SheetTrigger asChild>
              <Button variant="ghost" className="md:hidden" size="icon" aria-label="Open menu">
                <Menu className="h-5 w-5 sm:h-6 sm:w-full" />
              </Button>
            </SheetTrigger>
            <SheetContent side="right" className="w-[240px]">
              <div className="flex flex-col h-full w-full items-center text-center p-10">
                <div className="flex items-center mb-8">
                  <span className="font-bold text-lg">Randel Yumul</span>
                </div>
                <nav className="flex flex-col space-y-4">
                  {navLinks.map(({ href, label }) => (
                    <Link
                      key={href}
                      href={href}
                      className="text-foreground/80 hover:text-foreground transition-colors"
                    >
                      {label}
                    </Link>
                  ))}
                </nav>
              </div>
            </SheetContent>
          </Sheet>
        </div>
      </div>
    </header>
  );
}
